import { useContext } from 'react';
import { Container, Row, Col, Image } from 'react-bootstrap';
import Link from 'next/link';
import Slider from 'react-slick';

import AppContext from '../AppContext';

function FeaturedCategories(props) {
  const { categories } = useContext(AppContext);
  const settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 6,
    slidesToScroll: 1
  };

  return (
    <div className="FeaturedCategories">
      <Container>
        <Row>
          <Col>
            <Slider {...settings}>
              {
                categories.map(category => (
                  <div key={category.id}>
                    <Link href="!#">
                      <a className="FeaturedCategories-Item">
                        <Image 
                          fluid 
                          src={`./static/images/categories/${category.image}`} 
                          className="FeaturedCategories-Item__Image" 
                        />
                        <p className="FeaturedCategories-Item__Title">{category.title}</p>
                      </a>
                    </Link>
                  </div>
                ))
              }
            </Slider>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default FeaturedCategories;